<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'
  import Popper from 'popper.js'

  // Components
  import IconButton from '../components/IconButton.svelte'
  import Button from '../components/Button.svelte'
  import PopMenu from '../components/PopMenu.svelte'

  // Stores
  import { urlData } from '../stores/router'

  // Helpers
  import { signOut } from '../helpers/auth'

  export let title

  let button
  let pop
  let control
  let hide = true

  function toggleMenu() {
    hide = !hide
    if (control) control.update()
  }

  const popMenuList = [{ key: 'logout', label: 'Sair' }]

  function popMenuHandler(evt) {
    const item = evt.detail
    switch (item.key) {
      case 'logout':
        signOut()
        $urlData.segment = 'auth'
        break
    }
  }

  onMount(() => {
    control = new Popper(button, pop, { placement: 'bottom' })
  })
</script>

<style>
  div.app-bar {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    background-color: #fff;
  }

  div.logout div.top,
  div.logout div.menu {
    display: none;
  }

  div.top {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 0;
  }

  div.menu {
    z-index: 1;
  }

  h1 {
    text-align: center;
    opacity: 0.7;
  }

  .hide {
    display: none;
  }
</style>

<div
  transition:fade={{ duration: 300 }}
  class="app-bar"
  class:logout={$urlData.segment === 'auth'}>
  <h1 class="type-h3">{title}</h1>
  <div class="top" bind:this={button}>
    <IconButton icon="more_vert" on:click={toggleMenu} />

  </div>
  <div class="menu" bind:this={pop} class:hide>
    <PopMenu on:click={popMenuHandler} list={popMenuList} />
  </div>
</div>
